import React, { Component } from 'react';
import './Products.css';
import { fetchJson, CurrentOS, isNil } from '../functions/PublicFunctions'

const L = window.L;
const platforms = ['ios', 'android', 'mac', 'windows'];

class Products extends Component {
  clickProduct(event) {
    const { formTarget } = event.currentTarget;
    let { origin, pathname } = window.location;
    if (pathname[pathname.length - 1] !== '/') {
      pathname += '/';
    }
    const product = `${origin}${pathname}${formTarget}`;
    // console.log(product);
    window.location = product;
  }
  componentWithApps(apps){
    const list = apps.map((app) => 
          <li key={app.path}>
            <button className="ProductItem" onClick={this.clickProduct.bind(this)} formTarget={app.path}>
              <div className="icon"><img src={`/${app.path}Icon.png`} alt={app.name} /></div>
              <div className="title">{app.name}</div>
              <div className="subtitle">{app.version + ' ' + app.date}</div>
            </button>
          </li>
        );
    return (
      <ul>
        {list}
      </ul>
    );
  }
  loadApps(platform) {
    const key = `${platform}Apps`;
// this.setState({iosApps: this.componentWithApps(
//   [
//     {"name":"Zebra Alphabet Nothing Happend", "path":"/ios/alphabet/", "date":"02/13/2016", "version":"1.0.0.170522"},
//     {"name":"Web Reader", "path":"/ios/webreader/", "date":"01/13/2016", "version":"1.0.0.170522"},
//     {"name":"Zebra Alphabet", "path":"/ios/alphabet/", "date":"02/13/2016", "version":"1.0.0.170522"},
//     {"name":"Web Reader", "path":"/ios/webreader/", "date":"01/13/2016", "version":"1.0.0.170522"},
//   ]
// )});
    if (this.state[key].length > 0){
      return;
    }
    let newState = {};
    newState[key] = [<li key='loading'>Loading ...</li>];
    this.setState(newState);
    //
    const url = `/${platform}/apps.js`;
    fetchJson(url)
    .then(json => {
      newState[key] = this.componentWithApps(json);
      this.setState(newState);
    })
    .catch(error => {
      console.log("Load " +url + " filed!\n" + error.message);
      newState[key] = [<li className='noProducts' key='failed'>{L.get("NO_PRODUCTS", {platform: L.get(platform)})}</li>];
      this.setState(newState);
    });
  }
  // iosApps() { this.loadApps("ios"); }
  // androidApps() { this.loadApps("android"); }
  // macApps() { this.loadApps("mac"); }
  // windowsApps() { this.loadApps("windows"); }
  // winPhoneApps() { this.loadApps("winPhone"); }
  constructor(props) {
    super(props);
    this.state = {
      iosApps: [],
      androidApps: [],
      macApps: [],
      windowsApps: [],
      winPhoneApps: []
    };
    this.os = CurrentOS;
    if (isNil(this.os)){
      this.os = 'ios';
    }
  }
  componentDidMount() {
    platforms.map(plf => { 
      this.loadApps(plf);
      return 0;
    });
    // this.loadApps(this.os);
  }
  componentWillUnmount() {
  }
  render() {
    return (
      <div className="Products container">
        <div className="row">
          <nav className="col-sm-2 hidden-xs" id="platforms">
            <h3>{L.get("Platforms")}</h3>
            <ul className="nav nav-pills nav-stacked">
            {platforms.map(plf => {
              return (
                <li key={plf} className="col-sm-12 col-xs-auto" ><a href={`#${plf}`}>{L.get(plf)}</a></li>
              );
            })}
           </ul>
          </nav>
          <div className="col-sm-10 content">
            {platforms.map(plf => {
              return (
                <div id={plf} key={plf}>
                  <h4>{`${L.get(plf)} ${L.get('Products')}`}</h4>
                  <hr/>
                  <div>{this.state[`${plf}Apps`]}</div>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    );
    // return (
    //   <div className="container" >
    //     <h2>{L.get('Products')}</h2>
    //     <div className="Products" >
    //       <ul className="nav nav-tabs" role="tablist">
    //         <li className={`nav-item ${iosStatusClass}`} onClick={this.iosApps.bind(this)}>
    //           <a className="nav-link" data-toggle="tab" href="#ios" role="tab">iOS</a>
    //         </li>
    //         <li className={`nav-item ${androidStatusClass}`} onClick={this.androidApps.bind(this)}>
    //           <a className="nav-link" data-toggle="tab" href="#android" role="tab">Android</a>
    //         </li>
    //         <li className={`nav-item ${macStatusClass}`} onClick={this.macApps.bind(this)}>
    //           <a className="nav-link" data-toggle="tab" href="#mac" role="tab">Mac</a>
    //         </li>
    //         <li className={`nav-item ${windowsStatusClass}`} onClick={this.windowsApps.bind(this)}>
    //           <a className="nav-link" data-toggle="tab" href="#windows" role="tab">Windows</a>
    //         </li>
    //       </ul>

    //       <div className="tab-content">
    //         <div className={`tab-pane ${iosStatusClass}`} id="ios" role="tabpanel">
    //           {this.state.iosApps}
    //         </div>
    //         <div className={`tab-pane ${androidStatusClass}`} id="android" role="tabpanel">
    //           {this.state.androidApps}
    //         </div>
    //         <div className={`tab-pane ${macStatusClass}`} id="mac" role="tabpanel">
    //           {this.state.macApps}
    //         </div>
    //         <div className={`tab-pane ${windowsStatusClass}`} id="windows" role="tabpanel">
    //           {this.state.windowsApps}
    //         </div>
    //       </div>         
    //     </div>
    //   </div>
    // );
  }
}

export default Products;



