import React, { Component } from 'react';
// import Product from '../components/Product';
// import Carousel from '../components/Carousel'
import { fetchJson } from '../functions/PublicFunctions'
import './ProductDetails.css'

const L = window.L;
class ProductDetails extends Component {
  app = {};
  genAppBasicComponent(){
    const a = this.app;
    const dl_url = `itms-services://?action=download-manifest&amp;url=https://${window.location.host}${this.appPath}/${a.build.build}/Info.plist`;
    return (
      <div className="appBasic">
        <div className="media">
          <div className="media-left icon">
            <img src={`${this.appPath}/Icon.png`} className="media-object" alt={a.trackName}/>
          </div>
          <div className="media-body">
            <h3 className="media-heading text-primary">{a.trackName}<small><code>{a.contentAdvisoryRating}</code></small></h3>
            <div className="text-muted">
              {a.artistName}<br/>
              {a.primaryGenreName}
              <p className="install">
                <a className="btn btn-primary" href={dl_url}>{L.get("Install")}</a>
              </p>
            </div>
          </div>
        </div>
      </div>
    );
  }
  genAppScreenshotsComponent(){
    const images = [1,2,3,4,5,6].map(id => {
        return <img src={`${this.appPath}/ads/s${id}.jpg`} key={id} id={`i${id}`} className="img-thumbnail" alt=""/>;
    });
    return (
      <div className="screenshots">
        {images}
      </div>
    );
  }
  textWithFileSize(fsize) {
    let text = 'Bytes';
    if (fsize > 1024) {
      fsize = fsize / 1024;
      text = 'KB';
      if (fsize > 1024) {
        fsize = fsize / 1024; text = 'MB';
        if (fsize > 1024) {
          fsize = fsize / 1024; text = 'GB';
        }
      }
    }
    text = Number(fsize).toFixed(1) + text;
    return text;
  }
  genAppDetailsComponent(){
    const a = this.app;
    let idx = 0;
    return (
      <div className="appDetails row">
        <div className="col-sm-5 col-xs-12">
        <hr/>
          <h5>What's New</h5>
          <h6>{a.releaseDate}</h6>
          <p>{a.releaseNotes}</p>
        </div>
        <div className="col-sm-7 col-xs-12">
          <hr/>
          <h5>Description</h5>
          <p>{a.description}</p>
        </div>
        <div className="col-xs-12">
          <hr/>
          <h5>{L.get("Information")}</h5>
          <dl className="dl-horizontal">
            <dt>{L.get("Seller")}</dt><dd>{a.sellerName}</dd>
            <dt>{L.get("Categories")}</dt><dd>{a.genres.map(c => {
              return <span key={idx++}>{L.get(c)}, </span>;
              })}</dd>
            <dt>{L.get("Updated")}</dt><dd>{a.releaseDate}</dd>
            <dt>{L.get("Version")}</dt><dd>{a.version}</dd>
            <dt>{L.get("Size")}</dt><dd>{this.textWithFileSize(a.fileSizeBytes)}</dd>
            <dt>{L.get("Content")}</dt><dd>{a.contentAdvisoryRating}</dd>
            <dt>{L.get("Architectures")}</dt><dd>{a.build.arch}</dd>
            <dt>{L.get("Compatibility")}</dt><dd>Requires {a.build.requiredOS}, {a.supportedDevices.map(name => {
              return <span key={idx++}>{name}, </span>;
              })}</dd>
            <dt>{L.get("Languages")}</dt><dd>{a.languageCodesISO2A.map(code => {
              return <span key={idx++}>{L.get(code)}, </span>;
              })}</dd>
          </dl>     
          <p></p>
        </div>
      </div>
    );
  }
  loadAppInfo() {
    fetchJson(`${this.appPath}/lookup.json`)
    .then(json => {
      this.app = json.results[0];
      this.setState({appBasic: this.genAppBasicComponent(), 
        appScreenshots: this.genAppScreenshotsComponent(),
        appDetails: this.genAppDetailsComponent()});
    })
    .catch(error => {
      // this.setState({app: {}});
      console.log(error);
    });
  }
  constructor(){
    super();
    this.state = {appBasic: "", appScreenshots: "", appDetails: ""};
    // console.log("constructor");
  }
  componentDidMount() {
    // console.log("componentDidMount");
    this.loadAppInfo();
  }
  render() {
    // console.log("render");
    // console.log(this.props);
    // const { query } = this.props.location;
    const { platform, app } = this.props.match.params;
    this.appPath = `/${platform}/${app}`;
    // const appuidname = `${platform}${app}`;
    // const { plf, filter } = query;
    const navigationPath = (
      <ul className="breadcrumb navPath">
        <li><a href="/">{L.get("Home")}</a></li>
        <li><a href="/products">{L.get("Products")}</a></li>
        <li><a href={`/products#${platform}`}>{L.get(platform)}</a></li>
        <li className="active">App</li> 
      </ul>
      );
    return (
      <div className="container" >
        {navigationPath}
        <div className="Product" >
          {this.state.appBasic}
          <hr/>
          {this.state.appScreenshots}
          {this.state.appDetails}
        </div>
      </div>
    );
  }
}

export default ProductDetails;



