import React, { Component } from 'react';
// import './Contact.css';
class Contact extends Component {
  render() {
    const L = window.L;
    return (
      <div className="container">
        <h2>{L.get('CONTACT')}</h2>
        <h3>{L.get('WECHAT_OFFICIAL')}</h3>
        <p>{L.get("OFFICIAL_ACCOUNT")}</p>
        <p>{L.get("SCAN_OR")}</p>
        <img src="/res/qrcode-chulvkou.jpg" alt={L.get("SCAN_OR")}/>
        <br/>
        <h4><a href="mailto:alphabet_games@163.com"><span role="img" aria-label="Email"> {L.get("Email Us")}</span></a></h4>
        <p>&nbsp;</p>
      </div>
    );
  }
}

export default Contact;



