import React, { Component } from 'react';
import Carousel from '../components/Carousel'

const L = window.L;
class Home extends Component {
  jumbotronComponent(ad) {
    return (
      <div className="jumbotron">
          <div className="container">
          <h1 className="display-3">{ad.name}</h1>
          <p>{ad.detail}</p>
          <p><a className="btn btn-primary btn-lg" href={ad.url} target='_blank' rel='noopener noreferrer' role="button">{L.get('Learn more >>')}</a></p>
        </div> 
      </div>
    );
  }
  itemsComponent(ads) {
    let idx = 0;
    return ads.map(ad => {
      return (
      <div id={idx++} className="col-md-4">
        <h2>{ad.name}</h2>
        <p>{ad.detail}</p>
        <p><a className="btn btn-secondary" href={ad.url} target="_blank" rel='noopener noreferrer' role="button">{L.get("View details ")}</a></p>
      </div>
      );
    });
  }
  render() {
    // console.log(this.props.ads);
    let jumbo = "";
    let list = "";
    const { ads } = this.props;
    if ( ads.length > 0 ) {
      jumbo = this.jumbotronComponent(ads[0]);
      let more = [];
      for (let i = 1; i < ads.length; i++) {
        more[i-1] = ads[i];
      }
      list = this.itemsComponent(more);
    }
    return (
      <div className="container">
        <Carousel id="homeCarousel" dataPath="/home-ads/en.json"/>
        {jumbo}
        <div className="row">
          {list}
        </div>
      </div>
    );
  }
}

export default Home;



