import React, { Component } from 'react';
import './AdsBar.css';

class AdsBar extends Component {
  itemsComponent(items){
    if (items === undefined) {
      return "";
    }
    let idx = 0;
    return items.map((item) => 
          <a key={idx++} href={item.url} target='_blank' rel='noopener noreferrer'>
              <img src={item.icon} alt={item.title}  className="ad-icon" />
          </a>
        );
  }
  render() {
    // const L = window.Locales;
    return (
        <div className="ads-bar">
          {this.itemsComponent(this.props.ads)}
        </div>
    );
  }
}

export default AdsBar;


