import React, { Component } from 'react';
// import './About.css';

class About extends Component {
  render() {
    const L = window.L;
    return (
      <div className="container">
        <h2>{L.get('About')}</h2>
        <p>{L.get("ABOUT_DETAILS", {brand: L.get("BRAND")})}</p>
        <p>&nbsp;</p>
      </div>
    );
  }
}

export default About;



