import React, { Component } from 'react';
import AppHeader from './components/AppHeader';
import AppFooter from './components/AppFooter';
import AdsBar from './components/AdsBar';
import './App.css';
import Home from './pages/Home';
import About from './pages/About';
import Contact from './pages/Contact';
import Products from './pages/Products';
import ProductDetails from './pages/ProductDetails';
import NotFound from './pages/NotFound'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { fetchJson } from './functions/PublicFunctions'

class App extends Component {
    loadAds() {
    if (this.state.ads.length > 0) {
      return;
    }
    fetchJson('/ads/en.json')
    .then(json => {
      this.setState({ads: json});
    })
    .catch(error => {
      console.error(error);
    });
  }
  constructor() {
    super();
    this.state = {
      ads: []
    };
  }
  componentDidMount(){
    this.loadAds();
  }
  render() {
    return (
    <BrowserRouter>
      <div className="page-bootlint">
        <AppHeader/>
        <div className="AppContent">
          <Switch>
            <Route exact path="/" component={() => <Home ads={this.state.ads}/>}/> 
            <Route exact path="/about" component={About} /> 
            <Route exact path="/contact" component={Contact} /> 
            <Route exact path="/products" component={Products} />
            <Route exact path="/products/:platform/:app" component={ProductDetails} />
            <Route component={NotFound} />
          </Switch>
        </div>
        <AdsBar ads={this.state.ads}/>
        <AppFooter />
      </div>
    </BrowserRouter>
    );
  }
}

export default App;



